<template>
  <div>
    <v-container>
      <v-row>
        <v-col
          cols="12"
        >
          <v-card
            class="mx-auto"
            max-width="650"
          >
            <v-form>
              <v-card-title>
                <span class="text-h4 text-md-h3">Relatório de Lançamentos</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row justify="center">
                    <v-col
                      cols="12"
                      sm="6"
                      md="12"
                    >
                      <v-autocomplete
                        v-model="company"
                        :items="companies"
                        color="blue"
                        class="grey-label"
                        outlined
                        hide-details
                        label="Empresa"
                        prepend-icon="mdi-office-building-outline"
                        item-text="EMPRESA"
                        item-value="EMPRESA"
                      >
                        <template v-slot:no-data>
                          <v-list-item>
                            <v-list-item-title>
                              Nenhum item encontrado
                              <br><br>
                              <v-progress-linear
                                indeterminate
                                color="blue"
                              />
                            </v-list-item-title>
                          </v-list-item>
                        </template>
                      </v-autocomplete>
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="aetState"
                        color="blue"
                        class="grey-label"
                        hide-details
                        :items="aetStates"
                        prepend-icon="mdi-map-marker-outline"
                        label="Estado"
                        placeholder="Selecione"
                        outlined
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      md="6"
                    >
                      <v-select
                        v-model="aetStatus"
                        color="blue"
                        class="grey-label"
                        prepend-icon="mdi-list-status"
                        hide-details
                        multiple
                        :items="statusList"
                        label="Status"
                        placeholder="Selecione"
                        outlined
                      />
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-menu
                        class="d-flex"
                        min-width="auto"
                        offset-y
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedFeePaymentDateFormattedStart"
                            readonly
                            outlined
                            persistent-hint
                            max-width="100"
                            prepend-icon="mdi-cash"
                            label="Data Pagamento Taxa"
                            hint="Início"
                            class="my-0 ms-2 pa-0"
                            v-bind="attrs"
                            v-on="on"
                          />
                        </template>
                          <v-date-picker
                            v-model="feePaymentDateStart"
                            color="amber"
                            locale="pt-BR"
                            no-title
                            class="ma-0 pa-0 mx-auto"
                          />
                      </v-menu>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-menu
                        class="d-flex"
                        min-width="auto"
                        offset-y
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedFeePaymentDateFormattedEnd"
                            readonly
                            outlined
                            persistent-hint
                            max-width="100"
                            label="Data Pagamento Taxa"
                            hint="Final"
                            class="my-0 ms-2 pa-0"
                            v-bind="attrs"
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="feePaymentDateEnd"
                          color="amber"
                          locale="pt-BR"
                          no-title
                          class="ma-0 pa-0 mx-auto"
                        />
                      </v-menu>
                    </v-col>

                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-menu
                        class="d-flex"
                        min-width="auto"
                        offset-y
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedRegisterDateFormattedStart"
                            readonly
                            outlined
                            persistent-hint
                            max-width="100"
                            prepend-icon="mdi-cash-register"
                            label="Data Lançamento"
                            hint="Início"
                            class="my-0 ms-2 pa-0"
                            v-bind="attrs"
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="registerDateStart"
                          color="amber"
                          locale="pt-BR"
                          no-title
                          class="ma-0 pa-0 mx-auto"
                        />
                      </v-menu>
                    </v-col>
                    <v-col
                      cols="12"
                      sm="6"
                    >
                      <v-menu
                        class="d-flex"
                        min-width="auto"
                        offset-y
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="computedRegisterDateFormattedEnd"
                            readonly
                            outlined
                            persistent-hint
                            max-width="100"
                            label="Data Lançamento"
                            hint="Final"
                            class="my-0 ms-2 pa-0"
                            v-bind="attrs"
                            v-on="on"
                          />
                        </template>
                        <v-date-picker
                          v-model="registerDateEnd"
                          color="amber"
                          locale="pt-BR"
                          no-title
                          class="ma-0 pa-0 mx-auto"
                        />
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions class="mt-n10 px-10 py-5">
                <v-btn
                  fab
                  small
                  :disabled="!licences"
                  :loading="loading2"
                  @click="downloadPDF()"
                >
                  <v-icon
                    dark
                    color="red darken-2"
                  >
                    mdi-file-pdf-box
                  </v-icon>
                </v-btn>
                <v-btn
                  fab
                  small
                  :disabled="!licences"
                  :loading="loading3"
                  @click="downloadXLSX('xlsx')"
                >
                  <v-icon
                    dark
                    color="green darken-2"
                  >
                    mdi-microsoft-excel
                  </v-icon>
                </v-btn>
                <v-spacer />
                <v-btn
                  color="blue darken-3 white--text"
                  :loading="loading"
                  @click="generateReport(), loading = true"
                >
                  Gerar Relatório
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-container v-if="showReport === true">
          <v-data-table
            :headers="headers"
            :items="licences"
            :footer-props="{ itemsPerPageOptions: [5, 10, -1] }"
          />
        </v-container>
      </v-row>
    </v-container>
    <vue-html2pdf
      :show-layout="false"
      :enable-download="true"
      :manual-pagination="false"
      :paginate-elements-by-height="14001"
      :filename="reportName"
      :pdf-quality="2"
      pdf-format="a4"
      pdf-orientation="landscape"
      pdf-content-width="100%"
      @startPagination="loading2 = true"
      @hasDownloaded="loading2 = false"
      ref="html2Pdf"
    >
      <section slot="pdf-content">
        <v-container v-if="showReport === true">
          <v-data-table
            :headers="headers"
            :items="licences"
            :footer-props="{ itemsPerPageOptions: [-1] }"
          />
        </v-container>
      </section>
    </vue-html2pdf>
    <table
      v-show="false"
      ref="exportableTable"
    >
      <tr>
        <th
          v-for="(header, idx) in headers"
          :key="'header' + idx"
        >
          {{ header.text }}
        </th>
      </tr>
      <tr
        v-for="(licence, idx) in licences"
        :key="'data' + idx"
      >
        <td>{{ licence.ID }}</td>
        <td>{{ usDate(licence.DATA) }}</td>
        <td>{{ licence.SOLICITANTE }}</td>
        <td>{{ licence.EMPRESA }}</td>
        <td>{{ licence.PLACA }}</td>
        <td>{{ licence.ESTADO }}</td>
        <td>{{ licence.NUMTAXA }}</td>
        <td>{{ usDate(licence.DATAPAGAMENTOTAXA) }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  import axios from 'axios'
  import VueHtml2pdf from 'vue-html2pdf'
  import * as XLSX from 'xlsx/xlsx.mjs'

  export default {
    name: 'FinancialReportStaff',

    components: {
      VueHtml2pdf
    },

    data: () => ({
      company: '',
      aetState: '',
      plate: '',
      aetStatus: '',
      table: '',
      feePaymentDateFormatted: '',
      registerDateFormatted: '',
      reportName: '',
      showReport: false,
      licences: null,
      loading: false,
      loading2: false,
      loading3: false,
      registerDateStart: new Date(new Date().getFullYear(), new Date().getMonth(), 1).toISOString().substr(0, 10), // first day of the month
      registerDateEnd: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10), // today
      feePaymentDateStart: new Date(new Date().getFullYear(), new Date().getMonth(), 1).toISOString().substr(0, 10),
      feePaymentDateEnd: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      companies: [],
      aetStates: [
        { value: '', text: 'Selecione'},
        { value: 'FEDERAL', text: 'FEDERAL' },
        { value: 'BA', text: 'Bahia' },
        { value: 'GO', text: 'Goiás' },
        { value: 'MG', text: 'Minas Gerais' },
        { value: 'MS', text: 'Mato Grosso do Sul' },
        { value: 'PE', text: 'Pernambuco' },
        { value: 'PR', text: 'Paraná' },
        { value: 'RJ', text: 'Rio de Janiero' },
        { value: 'RS', text: 'Rio Grande do Sul' },
        { value: 'SC', text: 'Santa Catarina' },
        { value: 'SP', text: 'São Paulo' },
        { value: 'TO', text: 'Tocantins' },
        { value: 'AC', text: 'Acre' },
        { value: 'AL', text: 'Alagoas' },
        { value: 'AM', text: 'Amazonas' },
        { value: 'AP', text: 'Amapá' },
        { value: 'CE', text: 'Ceará' },
        { value: 'DF', text: 'Distrito Federal' },
        { value: 'ES', text: 'Espirito Santo' },
        { value: 'MA', text: 'Maranhão' },
        { value: 'MT', text: 'Mato Grosso' },
        { value: 'PA', text: 'Pará' },
        { value: 'PB', text: 'Paraíba' },
        { value: 'PI', text: 'Piauí' },
        { value: 'RN', text: 'Rio Grande do Norte' },
        { value: 'RO', text: 'Rondônia' },
        { value: 'RR', text: 'Roraima' },
        { value: 'SE', text: 'Sergipe' },
      ],
      headers: [
        { text: 'ID', value: 'ID' },
        { text: 'Lançamento', value: 'DATA' },
        { text: 'Status', value: 'SOLICITANTE' },
        { text: 'Empresa', value: 'EMPRESA' },
        { text: 'Placa', value: 'PLACA' },
        { text: 'UF', value: 'ESTADO' },
        { text: 'Taxa', value: 'NUMTAXA' },
        { text: 'Pagamento', value: 'DATAPAGAMENTOTAXA' },
      ],
      statusList: [
        { text: 'Taxa', value: 'TAXA' },
        { text: 'Não Saiu Nada', value: 'Não Saiu Nada' },
        { text: 'Emitido', value: 'Emitido' },
      ]
    }),

    computed: {
      computedRegisterDateFormattedStart () {
        return this.formatDate(this.registerDateStart)
      },
      computedRegisterDateFormattedEnd () {
        return this.formatDate(this.registerDateEnd)
      },
      computedFeePaymentDateFormattedStart () {
        return this.formatDate(this.feePaymentDateStart)
      },
      computedFeePaymentDateFormattedEnd () {
        return this.formatDate(this.feePaymentDateEnd)
      },
    },

    watch: {
      company () {
        let plate = this.companies.filter(item => (item.EMPRESA === this.company))
        this.plates = plate

        return this.plates
      },
      registerDateStart () {
        this.registerDateFormatted = this.formatDate(this.registerDateStart)
      },
      registerDateEnd () {
        this.registerDateFormatted = this.formatDate(this.registerDateEnd)
      },
      feePaymentDateStart () {
        this.feePaymentDateFormatted = this.formatDate(this.feePaymentDateStart)
      },
      feePaymentDateEnd () {
        this.feePaymentDateFormatted = this.formatDate(this.feePaymentDateEnd)
      },
      licences () {
        return this.reportName = 'Relatório NVS ' + this.registerDateEnd
      },
    },

    created() {
      this.fetchCompanies()
    },

    methods: {
      fetchCompanies () {
        return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((r) => r.json())
          .then((r) => {
            this.companies = r
          })
      },
      formatDate (date) {
        if (!date) return null
        var [year, month, day] = date.split('-')
        return `${day.padStart(2, '0')}/${month.padStart(2, '0')}/${year}`
      },
      parseDate (date) {
        if (!date) return null
        const [day, month, year] = date.split('/')
        return `${year}${month.padStart(2, '0')}${day.padStart(2, '0')}`
      },
      usDate (date) {
        if (!date) return null
        const [day, month, year] = date.split('/')
        var secs = '00:00:28'
        return `${month.padStart(2, '0')}/${day.padStart(2, '0')}/${year} ${secs}`
      },
      fetchLicences () {
        return axios.get('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
      },
      generateReport () {
        this.fetchLicences().then((r) => {
          var table = r.data

          this.$nextTick(() => {
            var totalResult = []
            var partialResult = []
            if (this.aetStatus.length === 0) {
              totalResult = table.filter(item =>
                (item.SOLICITANTE.includes(this.aetStatus) || item.SOLICITANTE.includes(this.aetStatus)) &&
                item.EMPRESA.includes(this.company) &&
                item.PLACA.includes(this.plate) &&
                item.ESTADO.includes(this.aetState) &&
                this.parseDate(item.DATAPAGAMENTOTAXA) >= this.feePaymentDateStart.replace(/-/g, '') &&
                this.parseDate(item.DATAPAGAMENTOTAXA) <= this.feePaymentDateEnd.replace(/-/g, '') &&
                this.parseDate(item.DATA) >= this.registerDateStart.replace(/-/g, '') &&
                this.parseDate(item.DATA) <= this.registerDateEnd.replace(/-/g, '')
              )
            } else {
              for (let i=0; i<this.aetStatus.length; i++) {
                partialResult = table.filter(item =>
                  (item.SOLICITANTE.includes(this.aetStatus) || item.SOLICITANTE.includes(this.aetStatus[i])) &&
                  item.EMPRESA.includes(this.company) &&
                  item.PLACA.includes(this.plate) &&
                  item.ESTADO.includes(this.aetState) &&
                  this.parseDate(item.DATAPAGAMENTOTAXA) >= this.feePaymentDateStart.replace(/-/g, '') &&
                  this.parseDate(item.DATAPAGAMENTOTAXA) <= this.feePaymentDateEnd.replace(/-/g, '') &&
                  this.parseDate(item.DATA) >= this.registerDateStart.replace(/-/g, '') &&
                  this.parseDate(item.DATA) <= this.registerDateEnd.replace(/-/g, '')
                )

                totalResult.push(...[...partialResult])
              }
            }

            this.licences = totalResult

            this.showReport = true
            this.loading = false
          })
        })
      },
      downloadPDF () {
        this.$refs.html2Pdf.generatePdf()
      },
      downloadXLSX (type, fn, dl) {
        var el = this.$refs.exportableTable
        var wb = XLSX.utils.table_to_book(el, {sheet:'Relatório'})
        this.loading3 = true
        setTimeout(() => {
          this.loading3 = false
          return dl ?
            XLSX.write(wb, {bookType: type, bookSTT: true, type: 'string'}) :
            XLSX.writeFile(wb, fn || (this.reportName + '.' +  (type || 'xlsx')))
        }, 100)
      },
    },
  }
</script>
